import { LOGIN_SUCESS, LOGOUT } from '../actions/login';

const initState = {
  isLogin: false,
  jwt: ''
};

export default (state = initState, action) => {
  switch (action.type) {
    case '@redux/INIT':
      return initState;
    case LOGIN_SUCESS:
      return Object.assign({}, state, { isLogin: true, jwt: action.payload.jwt });
    case LOGOUT:
      return Object.assign({}, state, { isLogin: false, jwt: '' });
    default:
      return state;
  }
};



