export const LOGIN_SUCESS = 'LOGIN_SUCCESS';
function loginSuccess(payload) {
  return {
    type: LOGIN_SUCESS,
    payload,
  };
}
export const LOGOUT = 'LOGOUT';
function logout() {
  return {
    type: LOGOUT
  };
}

export default { loginSuccess, logout };



