import {
  ALL_USERS,
  NORMAL_USERS,
  DOCTOR_USERS,
  MORE_ALL_USERS,
  MORE_DOCTOR_USERS,
  MORE_NORMAL_USERS,
  UPDATE_OFFSET_ALL_USERS,
  UPDATE_OFFSET_NORMAL_USERS,
  UPDATE_OFFSET_DOCTOR_USERS
} from '../actions/userList';

const initState = {
  allUsers: {
    userList: [],
    offset: 0,
    limit: 20
  },
  normalUsers: {
    userList: [],
    offset: 0,
    limit: 20
  },
  doctorUsers: {
    userList: [],
    offset: 0,
    limit: 20
  }
};

export default (state = initState, action) => {
  switch (action.type) {
    case '@@redux/INIT':
      return initState;
    case ALL_USERS: {
      const { offset, limit } = state.allUsers;
      return Object.assign({}, state, {
        allUsers: { userList: action.payload.userList, offset, limit }
      });
    }
    case NORMAL_USERS: {
      const { offset, limit } = state.normalUsers;
      return Object.assign({}, state, {
        normalUsers: { userList: action.payload.userList, offset, limit }
      });
    }
    case DOCTOR_USERS: {
      const { offset, limit } = state.doctorUsers;
      return Object.assign({}, state, {
        doctorUsers: { userList: action.payload.userList, offset, limit }
      });
    }
    case MORE_ALL_USERS: {
      const { userList } = state.allUsers;
      return Object.assign({}, state, {
        allUsers: { userList: userList.concat(action.payload.userList) }
      });
    }
    case MORE_DOCTOR_USERS: {
      const { userList } = state.doctorUsers;
      return Object.assign({}, state, {
        doctorUsers: { userList: userList.concat(action.payload.userList) }
      });
    }
    case MORE_NORMAL_USERS: {
      const { userList } = state.normalUsers;
      return Object.assign({}, state, {
        normalUsers: { userList: userList.concat(action.payload.userList) }
      });
    }
    case UPDATE_OFFSET_ALL_USERS: {
      return Object.assign({}, state, { allUsers: action.payload });
    }
    case UPDATE_OFFSET_DOCTOR_USERS: {
      return Object.assign({}, state, { doctorUsers: action.payload });
    }
    case UPDATE_OFFSET_NORMAL_USERS: {
      return Object.assign({}, state, { normalUsers: action.payload });
    }
    default:
      return state;
  }
};



