import React, { Component } from 'react';
import RichTextEditor from 'react-rte';
import { connect } from 'react-redux';
import userListModel from '../../models/userList';

class ListNguoidungthuong extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: RichTextEditor.createEmptyValue(),
      popup: {
        user: null,
        content: 'detail'
      },
      sortByType: {
        sortBy: 0, //0 => by id, 1: name, 2: status Lock, 3: by status Ticked //column of table
        sortIdUp: true,
        sortNameAToZ: true,
        sortStatusLock: true,
        sortStatusTick: true
      },
      userListAll: []
    };
  }
  componentWillMount() {
    this.props.getNormalUserList();
  }
  onChange = value => {
    this.setState({ value });
    if (this.props.onChange) {
      this.props.onChange(value.toString('html'));
    }
  };
  detailClick(e, user) {
    e.stopPropagation();
    this.setState({
      popup: {
        user,
        content: 'detail'
      }
    });
  }

  postClick(e, user) {
    e.stopPropagation();
    this.setState({
      popup: {
        user,
        content: 'post'
      }
    });
  }

  msgClick(e, user) {
    e.stopPropagation();
    this.setState({
      popup: {
        user,
        content: 'msg'
      }
    });
  }

  setSortById() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 0;
    sortBy.sortIdUp = !sortBy.sortIdUp;
    this.setState({
      sortByType: this.state.sortByType
    });
  }

  setSortByName() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 1;
    sortBy.sortNameAToZ = !sortBy.sortNameAToZ;
    this.setState({
      sortByType: this.state.sortByType
    });
  }

  setSortByLock() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 2;
    sortBy.sortStatusLock = !sortBy.sortStatusLock;
    this.setState({
      sortByType: this.state.sortByType
    });
  }

  setSortByTick() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 3;
    sortBy.sortStatusTick = !sortBy.sortStatusTick;
    this.setState({
      sortByType: this.state.sortByType
    });
  }

  toggle_tick_account(i) {
    const user = this.state.userListAll[i];
    user.tick = !user.tick;
    this.setState({
      userListAll: this.state.userListAll
    });
  }

  toggle_lock_account(e, user) {
    e.stopPropagation();
    user.lock = !user.lock;
    let x = window.confirm(
      (user.lock ? 'Bạn muốn khóa tài khoản: ' : 'Bạn muốn mở khóa tài khoản: ') + user.id + ' ?'
    );
    if (x) {
      this.setState({
        userListAll: this.state.userListAll
      });
    }
  }

  render() {
    const { popup, userListAll } = this.state;
    const { userList } = this.props;
    const popupUser = popup.user;

    return (
      <div className="list-all">
        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title d-flex justify-content-between">
              <span className="float-left">Tất cả</span>
              <div className="col-right">
                <span className="h-100 target-content-user mr-2">
                  <input
                    type="text"
                    name="search"
                    placeholder="Search.."
                    className="p-0 pl-2 m-0"
                  />
                </span>
                <span className="h-100">
                  <select name="" id="">
                    <option value="">Chọn thao tác</option>
                    <option value="">Gửi tin nhắn</option>
                    <option value="">Khóa</option>
                    <option value="">Mở khóa</option>
                  </select>
                </span>
              </div>
            </div>
            <div className="block-content-body">
              <div className="list-user position-relative">
                <div
                  className="popup modal fade"
                  id="list-popup"
                  tabIndex="-1"
                  role="dialog"
                  aria-labelledby="exampleModalCenterTitle"
                  aria-hidden="true"
                >
                  <div className="modal-dialog" role="document">
                    <div className="modal-content p-2">
                      {popupUser &&
                        (() => {
                          switch (popup.content) {
                            //popup chi tiết 1 bác sĩ
                            case 'detail':
                              return (
                                <div className="popup-detail">
                                  <div className="modal-header">
                                    <h3 className="modal-title" id="list-popup-title">
                                      Thông tin chi tiết của <b>{popupUser.fullName}</b>
                                    </h3>
                                    <button
                                      type="button"
                                      className="close"
                                      data-dismiss="modal"
                                      aria-label="Close"
                                    >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                    <table className="table table-hover">
                                      <thead className="thead-dark">
                                        <tr className="">
                                          <th>Mục</th>
                                          <th>Thông tin</th>
                                        </tr>
                                      </thead>
                                      <tbody>
                                        <tr>
                                          <td>ID</td>
                                          <td>{popupUser.id}</td>
                                        </tr>
                                        <tr>
                                          <td>Họ tên</td>
                                          <td>{popupUser.fullName}</td>
                                        </tr>
                                        <tr>
                                          <td>Chuyên khoa</td>
                                          <td>{popupUser.specialist}</td>
                                        </tr>
                                        <tr>
                                          <td>Email</td>
                                          <td>{popupUser.email}</td>
                                        </tr>
                                        <tr>
                                          <td>SĐT</td>
                                          <td>{popupUser.phoneNumber}</td>
                                        </tr>
                                        <tr>
                                          <td>Tình trạng</td>
                                          <td
                                            className={
                                              popupUser.status === 'online' ? 'text-success' : null
                                            }
                                          >
                                            {popupUser.status}
                                          </td>
                                        </tr>
                                      </tbody>
                                    </table>
                                  </div>
                                  <div className="modal-footer">
                                    <button
                                      type="button"
                                      className="btn btn-secondary"
                                      data-dismiss="modal"
                                    >
                                      Đóng
                                    </button>
                                  </div>
                                </div>
                              );
                            //popup danh sách bài viết của bác sĩ
                            case 'post':
                              return (
                                <div className="popup-post">
                                  <div className="modal-header">
                                    <h3 className="modal-title" id="list-popup-title">
                                      Danh sách bài viết của <b>{popupUser.fullName}</b>
                                    </h3>
                                    <button
                                      type="button"
                                      className="close"
                                      data-dismiss="modal"
                                      aria-label="Close"
                                    >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                    { popupUser.post && popupUser.post.map((elm, i) => (
                                      <div key={i} className="post-info mt-3 p-3 position-relative">
                                        <span
                                          className="delete-post position-absolute"
                                          title="Xóa bài đăng này"
                                        >
                                          <i className="fas fa-trash-alt" />
                                        </span>
                                        <div
                                          className="post-title p-2 clearfix"
                                          data-toggle="collapse"
                                          data-target={'#post-collapse' + i}
                                        >
                                          <div className="pt-2 pb-2">
                                            <b>Ngày đăng</b>: {elm.date}
                                            <span className="float-right">
                                              <i className="fas fa-sort-down" />
                                            </span>
                                          </div>
                                        </div>
                                        <div
                                          id={'post-collapse' + i}
                                          data-parent=".modal-body"
                                          className={'post-body collapse ' + (i ? null : 'show')}
                                          data-toggle="collapse"
                                        >
                                          <div className="post-content p-2">
                                            <b>Nội dung</b>:
                                          </div>
                                          <pre className="mb-2 mt-2 p-3">{elm.content}</pre>
                                          <hr />
                                          <div className="post-time-create p-2">
                                            <b>Bác sĩ được mời:</b>{' '}
                                            {elm.member.map((m, i) => (i ? m + '.' : m + ', '))}
                                          </div>
                                          <hr />
                                          <div className="show-comment clearfix">
                                            <button
                                              className="float-right p-2 m-2 border bg-hover-gainsboro"
                                              data-toggle="collapse"
                                              data-target={'#comment-collapse' + i}
                                            >
                                              Hiện bình luận
                                            </button>
                                          </div>
                                          <div
                                            id={'comment-collapse' + i}
                                            className="list-comment pl-2 ml-3 collapse position-relative"
                                          >
                                            {elm.comment.map((cmt, i) => (
                                              <div
                                                key={i}
                                                className="comment-info mt-2 mb-2 position-relative"
                                              >
                                                <div className="comment d-flex">
                                                  <span className="m-2 block-avatar">
                                                    <img
                                                      src={cmt.avatar}
                                                      alt="avatar"
                                                      width="40px"
                                                    />
                                                  </span>
                                                  <span className="m-2 text-comment p-3 position-relative">
                                                    {cmt.comment}
                                                  </span>
                                                  <span
                                                    className="delete-comment position-absolute d-none"
                                                    title="Xóa bài đăng này"
                                                  >
                                                    <i className="fas fa-trash-alt" />
                                                  </span>
                                                </div>
                                                <div className="list-reply ml-4 p-2">
                                                  {cmt.reply.map((r, i) => (
                                                    <div
                                                      key={i}
                                                      className="reply-comment d-flex pl-1 position-relative"
                                                    >
                                                      <span className="m-2 block-avatar">
                                                        <img
                                                          src={cmt.avatar}
                                                          alt="avatar"
                                                          width="40px"
                                                        />
                                                      </span>
                                                      <span className="m-2 text-comment p-3 position-relative">
                                                        {cmt.comment}
                                                      </span>
                                                      <span
                                                        className="delete-reply d-none"
                                                        title="Xóa bài đăng này"
                                                      >
                                                        <i className="fas fa-trash-alt" />
                                                      </span>
                                                    </div>
                                                  ))}
                                                </div>
                                              </div>
                                            ))}
                                          </div>
                                        </div>
                                      </div>
                                    ))}
                                  </div>
                                  <div className="modal-footer">
                                    <button
                                      type="button"
                                      className="btn btn-secondary"
                                      data-dismiss="modal"
                                    >
                                      Đóng
                                    </button>
                                  </div>
                                </div>
                              );
                            //popup gửi tin cho bác sĩ
                            case 'msg':
                              return (
                                <div className="popup-msg">
                                  <div className="modal-header">
                                    <h3 className="modal-title" id="list-popup-title">
                                      Gửi tin nhắn cho <b>{popupUser.fullName}</b>
                                    </h3>
                                    <button
                                      type="button"
                                      className="close"
                                      data-dismiss="modal"
                                      aria-label="Close"
                                    >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                    <form action="">
                                      <div className="mail-title">
                                        <label className="d-block p-2">
                                          Tiêu đề<span className="text-danger"> * </span>
                                          <span className="text-required text-danger text-hide">
                                            This field cannot be empty
                                          </span>
                                        </label>
                                        <input type="text" className="text-dark p-2 w-100" />
                                      </div>
                                      <div className="mail-content">
                                        <label className="d-block p-2">
                                          Nội dung<span className="text-danger"> * </span>
                                          <span className="text-required text-danger text-hide">
                                            This field cannot be empty
                                          </span>
                                        </label>
                                        <RichTextEditor
                                          value={this.state.value}
                                          onChange={this.onChange}
                                        />
                                      </div>
                                      <div className="mail-file">
                                        <input
                                          type="file"
                                          className="form-control-file"
                                          name="choose-file"
                                        />
                                      </div>
                                      <div className="mini-block-content collapse target-content-user">
                                        <table className="table table-hover">
                                          <thead className="thead-dark">
                                            <tr className="">
                                              <th className="">Id</th>
                                              <th className="">Họ và tên</th>
                                              <th className="">Chi tiết</th>
                                              <th className="" />
                                            </tr>
                                          </thead>
                                          <tbody>
                                            <tr>
                                              <td>Id con gà</td>
                                              <td>Con gà là cha con zịt</td>
                                              <td>Con gà có 2 cái chân</td>
                                              <td>
                                                <input type="checkbox" />
                                              </td>
                                            </tr>
                                            <tr>
                                              <td>Con vịt là mẹ con gà</td>
                                              <td>id con vịt</td>
                                              <td>Con vịt có 2 cái cánh</td>
                                              <td>
                                                <input type="checkbox" />
                                              </td>
                                            </tr>
                                            <tr>
                                              <td>Con gà đập con vịt</td>
                                              <td>id con chicken</td>
                                              <td>2 con 4 cái chân</td>
                                              <td>
                                                <input type="checkbox" />
                                              </td>
                                            </tr>
                                          </tbody>
                                        </table>
                                      </div>
                                      <div className="mail-button">
                                        <button
                                          className="p-2 mr-2 mt-2 border bg-hover-gainsboro"
                                          type="submit"
                                        >
                                          Gửi
                                        </button>
                                        <button className="p-2 mr-2 mt-2 border bg-hover-gainsboro">
                                          Xóa
                                        </button>
                                      </div>
                                    </form>
                                  </div>
                                  <div className="modal-footer">
                                    <button
                                      type="button"
                                      className="btn btn-secondary"
                                      data-dismiss="modal"
                                    >
                                      Đóng
                                    </button>
                                  </div>
                                </div>
                              );
                            default:
                              return 'Error';
                          }
                        })()}
                    </div>
                  </div>
                </div>

                <table className="table table-hover">
                  <thead className="thead-dark">
                    <tr className="">
                      <th onClick={() => this.setSortById()}>
                        Id
                        <span className="float-right">
                          <i
                            className={
                              'fas ' +
                              (this.state.sortByType.sortIdUp ? 'fa-sort-down' : 'fa-sort-up')
                            }
                          />
                        </span>
                      </th>
                      <th onClick={() => this.setSortByName()}>
                        Họ và tên
                        <span className="float-right">
                          <i
                            className={
                              'fas ' +
                              (this.state.sortByType.sortNameAToZ ? 'fa-sort-down' : 'fa-sort-up')
                            }
                          />
                        </span>
                      </th>
                      <th>Email</th>
                      <th />
                      <th className="text-center" onClick={() => this.setSortByLock()}>
                        Khóa tài khoản
                        <span className="float-right">
                          <i
                            className={
                              'fas ' +
                              (this.state.sortByType.sortStatusLock ? 'fa-sort-down' : 'fa-sort-up')
                            }
                          />
                        </span>
                      </th>
                      <th className="text-center" onClick={() => this.setSortByTick()}>
                        Đánh dấu
                        <span className="float-right">
                          <i
                            className={
                              'fas ' +
                              (this.state.sortByType.sortStatusTick ? 'fa-sort-down' : 'fa-sort-up')
                            }
                          />
                        </span>
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {userList.map((elm, i) => (
                      <tr key={i} onClick={() => this.toggle_tick_account(i)}>
                        <td>{elm.id}</td>
                        <td>{elm.fullName}</td>
                        <td>{elm.email}</td>
                        <td className="d-flex justify-content-around text-center">
                          <span
                            title="Email"
                            onClick={e => this.detailClick(e, elm)}
                            data-toggle="modal"
                            data-target="#list-popup"
                          >
                            <i className="fas fa-info-circle" />
                          </span>
                          <span
                            title="Các bài viết"
                            onClick={e => this.postClick(e, elm)}
                            data-toggle="modal"
                            data-target="#list-popup"
                          >
                            <i className="fas fa-th-list" />
                          </span>
                          <span
                            title="Gửi tin nhắn"
                            onClick={e => this.msgClick(e, elm)}
                            data-toggle="modal"
                            data-target="#list-popup"
                          >
                            <i className="far fa-envelope" />
                          </span>
                        </td>
                        <td className="text-center">
                          {elm.isBlocked ? (
                            <span
                              title="Tài khoản đang khóa"
                              onClick={e => this.toggle_lock_account(e, elm)}
                              className="icon-lock"
                            >
                              <i className="fas fa-lock" />
                            </span>
                          ) : (
                            <span
                              title="Tài khoản đang mở"
                              onClick={e => this.toggle_lock_account(e, elm)}
                              className="icon-unlock"
                            >
                              <i className="fas fa-lock-open" />
                            </span>
                          )}
                        </td>
                        <td className="text-center">
                          <span>
                            <i
                              className={elm.tick ? 'fas fa-check' : 'text-secondary far fa-square'}
                            />
                          </span>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}


const mapStateToProps = state => ({ userList: state.userList.normalUsers.userList });
const mapDispatchToProps = dispatch => ({
  getNormalUserList: () => dispatch(userListModel.getNormalUsers()),
});
export default connect(mapStateToProps, mapDispatchToProps)(ListNguoidungthuong);



