export const ALL_USERS = 'ALL_USERS';
function allUsers(payload) {
  return {
    type: ALL_USERS,
    payload
  };
}
export const MORE_ALL_USERS = 'MORE_ALL_USERS';
function moreAllUsers(payload) {
  return {
    type: MORE_ALL_USERS,
    payload
  };
}
export const UPDATE_OFFSET_ALL_USERS = 'UPDATE_OFFSET_ALL_USERS';
function updateOffsetAllUsers(payload) {
  return {
    type: UPDATE_OFFSET_ALL_USERS,
    payload
  };
}

export const NORMAL_USERS = 'NORMAL_USERS';
function normalUsers(payload) {
  return {
    type: NORMAL_USERS,
    payload
  };
}
export const MORE_NORMAL_USERS = 'MORE_NORMAL_USERS';
function moreNormalUsers(payload) {
  return {
    type: MORE_NORMAL_USERS,
    payload
  };
}
export const UPDATE_OFFSET_NORMAL_USERS = 'UPDATE_OFFSET_NORMAL_USERS';
function updateOffsetNormalUsers(payload) {
  return {
    type: UPDATE_OFFSET_NORMAL_USERS,
    payload
  };
}

export const DOCTOR_USERS = 'DOCTOR_USERS';
function doctorUsers(payload) {
  return {
    type: DOCTOR_USERS,
    payload
  };
}
export const MORE_DOCTOR_USERS = 'MORE_DOCTOR_USERS';
function moreDoctorUsers(payload) {
  return {
    type: MORE_DOCTOR_USERS,
    payload
  };
}
export const UPDATE_OFFSET_DOCTOR_USERS = 'UPDATE_OFFSET_DOCTOR_USERS';
function updateOffsetDoctorUsers(payload) {
  return {
    type: UPDATE_OFFSET_DOCTOR_USERS,
    payload
  };
}

export default {
  allUsers,
  doctorUsers,
  normalUsers,
  moreAllUsers,
  moreDoctorUsers,
  moreNormalUsers,
  updateOffsetAllUsers,
  updateOffsetDoctorUsers,
  updateOffsetNormalUsers
};



