import React, { Component } from 'react';
import RichTextEditor from 'react-rte';
import { connect } from 'react-redux';

class SendNoteNguoidungthuong extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active_choose_user_to_send: false,
      value: RichTextEditor.createEmptyValue(),
      sortByType: {
        sortBy: 0, //0 => by id, 1: name, 2: status Lock, 3: by status Ticked //column of table
        sortIdUp: true,
        sortNameAToZ: true,
        sortStatusLock: true,
        sortStatusTick: true
      },
      userList: this.props.userList
    };
  }

  onChange = value => {
    this.setState({ value });
  };

  toggle_choose_user = () => {
    this.setState({
      active_choose_user_to_send: !this.state.active_choose_user_to_send
    });
  };

  setSortById() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 0;
    sortBy.sortIdUp = !sortBy.sortIdUp;
    this.setState({
      sortByType: this.state.sortByType
    });
  }
  setSortByName() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 1;
    sortBy.sortNameAToZ = !sortBy.sortNameAToZ;
    this.setState({
      sortByType: this.state.sortByType
    });
  }
  setSortByLock() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 2;
    sortBy.sortStatusLock = !sortBy.sortStatusLock;
    this.setState({
      sortByType: this.state.sortByType
    });
  }
  setSortByTick() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 3;
    sortBy.sortStatusTick = !sortBy.sortStatusTick;
    this.setState({
      sortByType: this.state.sortByType
    });
  }

  toggle_tick_account(i) {
    const user = this.state.userList[i];
    user.tick = !user.tick;
    this.setState({
      userList: this.state.userList
    });
  }

  toggle_lock_account(e, i) {
    e.stopPropagation();
    const user = this.state.userList[i];
    user.lock = !user.lock;
    var x = window.confirm(
      (user.lock ? 'Bạn muốn khóa tài khoản: ' : 'Bạn muốn mở khóa tài khoản: ') + user.id + ' ?'
    );
    if (x) {
      this.setState({
        userList: this.state.userList
      });
    } else {
    }
  }

  render() {
    return (
      <div className="send-note-nguoidungthuong">
        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title clearfix">
              <span>Gửi mail cho người dùng thường</span>
            </div>
            <div className="block-content-body">
              <form action="">
                <div className="mail-title">
                  <label className="d-block p-2">
                    Tiêu đề<span className="text-danger"> * </span>
                    <span className="text-required text-danger text-hide">
                      This field cannot be empty
                    </span>
                  </label>
                  <input type="text" className="text-dark p-2 w-100" />
                </div>
                <div className="mail-content">
                  <label className="d-block p-2">
                    Nội dung<span className="text-danger"> * </span>
                    <span className="text-required text-danger text-hide">
                      This field cannot be empty
                    </span>
                  </label>
                  <RichTextEditor value={this.state.value} onChange={this.onChange} />
                </div>
                <div className="mail-file">
                  <input type="file" className="form-control-file" name="choose-file" />
                </div>
                <div className="user-received p-2 d-flex justify-content-between position-relative">
                  <span>Lựa chọn người gửi:</span>
                  <span className="target-content-user">
                    <input
                      type="text"
                      name="search"
                      placeholder="Search.."
                      className="p-0 pl-2 m-0"
                    />
                  </span>
                </div>
                <div className="mini-block-content target-content-user">
                  <table className="table table-hover">
                    <thead className="thead-dark">
                      <tr className="">
                        <th onClick={() => this.setSortById()}>
                          Id
                          <span className="float-right">
                            <i
                              className={
                                'fas ' +
                                (this.state.sortByType.sortIdUp ? 'fa-sort-down' : 'fa-sort-up')
                              }
                            />
                          </span>
                        </th>
                        <th onClick={() => this.setSortByName()}>
                          Họ và tên
                          <span className="float-right">
                            <i
                              className={
                                'fas ' +
                                (this.state.sortByType.sortNameAToZ ? 'fa-sort-down' : 'fa-sort-up')
                              }
                            />
                          </span>
                        </th>
                        <th>Chi tiết</th>
                        <th className="text-center" onClick={() => this.setSortByTick()}>
                          Đánh dấu
                          <span className="float-right">
                            <i
                              className={
                                'fas ' +
                                (this.state.sortByType.sortStatusTick
                                  ? 'fa-sort-down'
                                  : 'fa-sort-up')
                              }
                            />
                          </span>
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.userList.map((elm, i) => (
                        <tr key={i} onClick={() => this.toggle_tick_account(i)}>
                          <td>{elm.id}</td>
                          <td>{elm.name}</td>
                          <td>{elm.detail}</td>
                          <td className="text-center">
                            <span>
                              <i
                                className={
                                  elm.tick ? 'fas fa-check' : 'text-secondary far fa-square'
                                }
                              />
                            </span>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
                <div className="mail-button">
                  <button className="p-2 mr-2 mt-2 border bg-hover-gainsboro" type="submit">
                    Gửi
                  </button>
                  <button className="p-2 mr-2 mt-2 border bg-hover-gainsboro">Xóa</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({ userList: state.userList.normalUsers.userList });

export default connect(mapStateToProps)(SendNoteNguoidungthuong);



