import React, { Component } from 'react';
import { connect } from 'react-redux';
import loginModel from '../models/login';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: 'admin@caremaster.tk',
      password: 'Aa123456'
    };
  }

  loginClick() {
    const { email, password } = this.state;
    this.props.login(email, password);
  }

  render() {
    const { email, password } = this.state;
    return (
      <div className="wrapper">
        <div className="login-page border border-dark w-100 h-100">
          <div className="block-content">
            <h2 className="text-center">CareMaster admin</h2>
            <hr />
            <div className="row tab-login">
              <div className="col-6 text-center item p-4">
                <p className="pb-3 active">Đăng nhập</p>
              </div>
              <div className="col-6 text-center item p-4">
                <p className="pb-3">Đăng ký</p>
              </div>
            </div>
            <div className="form-login">
              <div className="form-input account clearfix p-2">
                <label htmlFor="account">Tài khoản</label>
                <input
                  id="account"
                  type="text"
                  className="float-right"
                  value={email}
                  onChange={e => this.setState({ email: e.target.value })}
                />
              </div>
              <div className="form-input password clearfix p-2">
                <label htmlFor="password">Mật khẩu</label>
                <input
                  id="password"
                  type="password"
                  className="float-right"
                  value={password}
                  onChange={e => this.setState({ password: e.target.value })}
                />
              </div>
              <div className="login-button mb-3 mt-3">
                <button className="p-3 w-100" onClick={() => this.loginClick()}>
                  Đăng nhập
                </button>
              </div>
              <div className="forgot-password-button mb-3 mt-3">
                <button className="p-3 w-100">Quên mật khẩu</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  login: (email, password) => dispatch(loginModel.login(email, password))
});
export default connect(null, mapDispatchToProps)(Login);



