import userListAction from '../actions/userList';
import request from '../api/request';

export default {
  getAllUsers: () => (dispatch, getState) => {
    const state = getState();
    const { jwt } = state.loginState;
    const { offset, limit } = state.userList.allUsers;
    request(`/get-all-user/${offset}/${limit}`, null, 'GET', jwt).then(res => {
      const { message } = res;
      if (message === 'ADMIN_GET_ALL_USER_SUCCESSFULLY') {
        dispatch(userListAction.allUsers({ userList: res.data.users }));
      }
    });
  },
  getMoreAllUsers: () => dispatch => {},
  getDoctorUsers: () => (dispatch, getState) => {
    const state = getState();
    const { jwt } = state.loginState;
    const { offset, limit } = state.userList.doctorUsers;
    request(`/get-doctor-list/${offset}/${limit}`, null, 'GET', jwt).then(res => {
      const { message } = res;
      if (message === 'ADMIN_GET_DOCTOR_LIST_SUCCESSFULLY') {
        dispatch(userListAction.doctorUsers({ userList: res.data.doctors }));
      }
    });
  },
  getMoreDoctorUsers: () => dispatch => {},
  getNormalUsers: () => (dispatch, getState) => {
    const state = getState();
    const { jwt } = state.loginState;
    const { offset, limit } = state.userList.allUsers;
    request(`/get-user-list/${offset}/${limit}`, null, 'GET', jwt).then(res => {
      const { message } = res;
      console.log(res);
      if (message === 'ADMIN_GET_USER_LIST_SUCCESSFULLY') {
        dispatch(userListAction.normalUsers({ userList: res.data.users }));
      }
    });
  },
  getMoreNormalUsers: () => dispatch => {},
};



